body {
  margin: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  /* change these top variables to change the theme! */
  --primary-hue: 190;
  --light-angle: 65deg;
  --monochrome-alpha: 63%;
  
  --bg: hsl(var(--primary-hue), 50%, 30%);
  --bg-dark: hsl(var(--primary-hue), 50%, 20%);
  --heading-text: hsl(calc(var(--primary-hue) - 45), 100%, 80%);
  --heading-bg: hsla(calc(var(--primary-hue) - 15), 100%, 65%, 20%);
  --gray88: hsla(0, 0%, 88%, var(--monochrome-alpha));
  --white-alpha: hsla(0, 0%, 100%, var(--monochrome-alpha));
  --gray50: hsl(0, 0%, 50%);
  background: linear-gradient(65deg, hsl(190, 50%, 20%), hsl(190, 50%, 30%) 200%);
  background: linear-gradient(var(--light-angle), var(--bg-dark), var(--bg) 200%);
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  color: hsl(calc(190 - 45), 100%, 80%);
  color: var(--heading-text);
  margin: .25em 0;
}

ul {
  padding: 0;
  list-style: none;
}

input {
  width: 120px;
}

.CitySearch,
.NumberOfEvents,
.EventList {
  display: flex;
  flex-direction: column;
  width: 90%;
  margin: 0 5%;;
}

.CitySearch,
.NumberOfEvents,
.Event {
  background: hsla(0, 0%, 88%, 63%) linear-gradient(65deg, hsla(0, 0%, 88%, 63%), hsla(0, 0%, 100%, 63%));
  background: var(--gray88) linear-gradient(var(--light-angle), var(--gray88), var(--white-alpha));
  padding: 0 1.25em .5em;
  margin-bottom: .35em;
  border: 1px solid hsl(0, 0%, 50%);
  border: 1px solid var(--gray50);
  border-radius: .25em;
  box-shadow: 0 .2em hsla(0, 0%, 0%, 40%);
  
}

.CitySearch {margin: 1.25em 0;}

button {
  padding: .55em 1.25em;
}

.subtitle { margin-top: 0;}

.text-white {color: white;}

.ThemeChanger {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.ThemeChanger button {
  background-color: hsla(0, 0%, 88%, 63%);
  background-color: var(--gray88);
}

.ThemeChanger button:active {
  background-color: hsla(0, 0%, 100%, 63%);
  background-color: var(--white-alpha);
}

.theme-number {
  color: hsla(0, 0%, 100%, 63%);
  color: var(--white-alpha);
}

.TopBar {
  text-align: center;
  position: -webkit-sticky;
  position: sticky;
  width: 100%;
  border-bottom: 1.1px solid hsl(0, 0%, 50%);
  border-bottom: 1.1px solid var(--gray50);
  background: hsla(calc(190 - 15), 100%, 65%, 20%);
  background: var(--heading-bg);
}
/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: var(--heading-text);

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 2px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px var(--heading-text), 0 0 5px var(--heading-text);
  opacity: 1.0;

  -webkit-transform: rotate(3deg) translate(0px, -4px);
          transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 15px;
  left: 15px;
}

#nprogress .spinner-icon {
  width: 18px;
  height: 18px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: var(--heading-text);
  border-left-color: var(--heading-text);
  border-radius: 50%;

  -webkit-animation: nprogress-spinner 400ms linear infinite;
          animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}

@-webkit-keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}
@keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

